import React from 'react';

import { Container } from './styles';

interface Props {
  onPrev?: Function;
  onNext?: Function;
  disabledNext?: boolean;
  disabledPrev?: boolean;
}

const Actions = ({ onPrev, onNext, disabledNext, disabledPrev }: Props) => {
  return (
    <Container>
      <div>
        {onPrev && (
          <button
            onClick={() => onPrev()}
            type="button"
            disabled={disabledPrev}
          >
            Prev
          </button>
        )}
      </div>
      <div />
      <div className="next">
        {onNext && (
          <button
            onClick={() => onNext()}
            type="button"
            disabled={disabledNext}
          >
            Next
          </button>
        )}
      </div>
    </Container>
  );
};

export default Actions;
